.wrapper{
  padding-top: 36px;
  min-height: 650px;
  background: radial-gradient(75% 25% at 25% 25%, rgba(26, 21, 21, 0) 0%, rgba(1, 1, 1, 0.05) 100%), #FFFFFF;
  mix-blend-mode: normal;
}
.inner{
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 50px;
}
.name{
  width: 200px;
  font-weight: 700;
  font-size: 47px;
  line-height: 58px;
  color:#070707;
}

.pdfLink{
  display: block;
  padding-top: 10px;
}

.info{
  width: 200px;
  font-size: 20px;
  line-height: 26px;
  margin-bottom: 10px;
  span{
    display: block;
    font-weight: bold;
  }
}
.photoContainer{
  height: 400px;
  width: 100%;
  overflow: hidden;
}
.photo{
  transform: translateY(-30px);
  object-fit: cover;
}

@media (max-width: 576px) {

.wrapper{
  padding-top: 0;
}
  .inner{
    padding-top: 15px;
    flex-direction: column;
    margin-bottom: 25px;
  }
  .name{
    width: 350px;
    font-size: 38px;
    text-align: left;
    margin-bottom: 20px;
    align-self: flex-start;
  }
  .info{
    align-self: flex-start;
    text-align: left;
    width: 225px;
  }
  .photo{
    transform: translateY(0px);
  }
}
@media (max-width: 400px) {
  .name {
    width: 285px;
  }
}
